import React, { useState } from 'react';
import './styles/Home.css';
import './styles/Mobile.css';
import { Link } from 'react-router-dom';
import { useNavigate } from 'react-router-dom';

function Identifiants() {
    const [pseudo, setPseudo] = useState("");
    const [phraseRegister, setPhraseRegister] = useState("");
    const [newPassword, setNewPassword] = useState("");
    const [errorMessage, setErrorMessage] = useState("");
    const [successMessage, setSuccessMessage] = useState(""); // Nouvel tat pour le message de succs
    const navigate = useNavigate();

    const handleSubmit = async (e) => {
        e.preventDefault();
        const userData = {
            pseudo,
            phraseRegister,
            newPassword
        };
        try {
            const API_URL = window.location.hostname === 'localhost'
                ? "http://localhost:4000"
                : "https://myday-back.onrender.com";

            const response = await fetch(`${API_URL}/IdentifiantsMdp`, {
                method: "POST",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify(userData),
                credentials: 'include'
            });

            const data = await response.json(); // Attendre la rponse avant de la manipuler

            if (data.message) {
                setErrorMessage(data.message); // Afficher l'erreur
            } else {
                setErrorMessage(""); // 
                // setPseudo("");
                setPhraseRegister("");
                setNewPassword("");
                // Ici, tu peux grer le succs
                setSuccessMessage("Mot de passe rinitialis avec succs !");
                // Raliser la redirection vers la page de connexion aprs un dlai
                setTimeout(() => {
                    navigate('/Login');
                }, 2000); // Redirige aprs 2 secondes
            }
        } catch (error) {
            console.error("Erreur lors de l'envoi :", error);
            setErrorMessage("Erreur lors de la demande, veuillez ressayer.");
        }
    };

    return (
        <div className="container">
            <div className="header">
                <h1>
                    <span>M</span><span>y</span><span>D</span><span>a</span><span>y</span>
                </h1>
            </div>

            <h5>Identifiants</h5>
            <p>In progress </p>
            <form onSubmit={handleSubmit}>
                <h4>
                    <label htmlFor="pseudo">Pseudo: </label>
                    <input
                        className="login-input"
                        type="text"
                        id="pseudo"
                        name="pseudo"
                        value={pseudo}
                        onChange={(e) => setPseudo(e.target.value)}
                        required
                    />
                </h4>
                <h4>
                    <select
                        id="phraseRegister"
                        className="phrase-select"
                        value={phraseRegister}
                        onChange={(e) => setPhraseRegister(e.target.value)}
                    >
                        <option>Choisir une phrase.</option>
                        <option value="pizza">Je suis l'lement principal de la pizza.</option>
                        <option value="ia">Mon cerveau est la meilleure de IA.</option>
                        <option value="courant">Je prfre marcher  contre-courant.</option>
                        <option value="toi">Toi sans moi, a ne sera jamais nous.</option>
                    </select>
                </h4>
                <h4>
                    <label htmlFor="newPassword">Nouveau mot de passe: </label>
                    <input
                        className="login-input"
                        type="password"
                        id="newPassword"
                        name="newPassword"
                        value={newPassword}
                        onChange={(e) => setNewPassword(e.target.value)}
                        required
                    />
                </h4>
                <div className="boutton-clear-submit-index">
                    <button type="submit" className="submit-button">
                        Rinitialiser le mot de passe 
                    </button>
                </div>
                <hr className="hr" />
            </form>

            {/* Affichage du message de succs en pop-up */}
            {successMessage && (
                <div className="popup-success">
                    <p>{successMessage}</p>
                </div>
            )}

            <Link to="/Login">
                <button type="button" className="submit-button">
                    Connexion
                </button>
            </Link>

            <p className ="droits"> 2025 myDay. Tous droits rservs.
            Cette application, ainsi que lensemble de son contenu, est protge par les lois en vigueur relatives  la proprit intellectuelle. Les donnes quelle contient sont chiffres afin den garantir la scurit. </p>  
        </div>
    );
}

export default Identifiants;
